export default {
	data() {
		return {
			codeUrl:''
		}
	},
	methods: {
		saveImg(){
			// uni.saveImageToPhotosAlbum({
			// 	filePath:
			// })
		},
		getCode(){
			var that = this
			uni.request({
				url: that.baseUrl + 'index/Extension',
				data:{
					user_id: that.globalData.userInfo.user_id
				},
				method:'GET',
				success(res) {
					that.codeUrl = that.globalData.imgUrl + res.data.data
				}
				
			})
		}
	},
	onLoad() {
		this.getCode()
	},
	
	created() {
		
	}
}
